*{box-sizing: border-box;}
*:before, *:after{box-sizing: border-box;}
*{margin: 0; padding: 0;}
ul, ol{list-style: none;}
a{color: inherit; text-decoration: none;}

body{
    background-color: #eee;
}
img{
    width: 40px;
    height: 40px;
}

.globalHeader{
    margin: 20px;  
}
@media (min-width: 500px) {
    .globalHeader{
        margin: 60px 0 100px;
    }
}
.searchForm{
    display: flex;
    justify-content: space-between;
}
@media (min-width: 500px) {
    .searchForm{
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
.searchForm > input{
    width: 100%;
    margin-right: 10px;
    padding: 0 10px;
    height: 40px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 28px;
    border: none;
    border-radius: 4px;
    background: #067ABB;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}
.siteList{
    display: flex;
    flex-wrap: wrap;
    margin: 20px; 
    justify-content: space-between;
}
@media (min-width: 500px) {
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
    }   
}
.siteList > li{
    margin-bottom: 10px;
}
@media (min-width: 500px) {
    .siteList > li{
        margin-right: 25px;
    } 
}
.siteList .site{
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
}
.siteList .site > .logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
}
.siteList .site > .link{
    font-size: 14px;
    margin-top: 4px;
}
.siteList .site > .close{
    position: absolute;
    right: 10px;
    top: 5px;
    display: none;
    cursor: default;
}
.siteList .site > .tip{
    position: absolute;
    top: -30px;
    display: none;
    cursor: default;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 5px lightgray;
    white-space: nowrap;
    padding: 5px;
    background: white;
    font-size: 2px;
}
@media (min-width: 500px) {
    .siteList .site:hover > .close{
        display: block;   
    }
    .siteList .site:hover > .tip{
        display: block;   
    }
}
.siteList .addButton{
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 20px 0;
    cursor: pointer;
}
.siteList .addButton .icon{
    width: 60px;
    height: 60px;
}
.siteList .addButton .icon-wrapper{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.siteList .addButton .text{
    font-size: 14px;
    margin-top: 4px;
}